// frontend/src/app/admin/AdminLayoutClient.tsx
"use client";

import React, { ReactNode, useState } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { Menu, X } from "lucide-react";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";

interface AdminLayoutClientProps {
  children: ReactNode;
  onLogout?: () => void;
}

export default function AdminLayoutClient({ children, onLogout }: AdminLayoutClientProps) {
  const pathname = usePathname();
  const [sidebarOpen, setSidebarOpen] = useState(false);

  // React Query client
  const [queryClient] = useState(() => new QueryClient());

  const navItems = [
    { label: "Dashboard", href: "/admin" },
    { label: "Jobs", href: "/admin/jobs" },
    { label: "Applications", href: "/admin/applications" },
    { label: "Pages", href: "/admin/pages" },
  ];

  return (
    <QueryClientProvider client={queryClient}>
      <div className="flex min-h-screen bg-violet-100">
        {/* Sidebar */}
        <aside
          className={`fixed inset-y-0 left-0 z-20 w-64 bg-white shadow-lg transform transition-transform duration-300 ease-in-out
            ${sidebarOpen ? "translate-x-0" : "-translate-x-full"} md:translate-x-0 md:static md:inset-auto`}
        >
          <div className="h-16 flex items-center justify-center shadow-md font-bold text-xl border-b border-gray-200">
            Admin Panel
          </div>

          <nav className="p-4 space-y-2">
            {navItems.map((item) => (
              <Link
                key={item.href}
                href={item.href}
                className={`block px-4 py-2 rounded hover:bg-gray-200 ${
                  pathname === item.href ? "bg-yellow-200 font-semibold" : ""
                }`}
              >
                {item.label}
              </Link>
            ))}

            {onLogout && (
              <button
                onClick={onLogout}
                className="w-full text-left px-4 py-2 mt-4 rounded bg-red-600 text-black hover:bg-red-700"
              >
                Logout
              </button>
            )}
          </nav>
        </aside>

        {/* Main content */}
        <div className="flex-1 flex flex-col min-h-screen md:pl-64">
          {/* Top navbar (mobile) */}
          <header className="h-16 flex items-center justify-between px-4 bg-white shadow-md md:hidden">
            <div className="font-bold text-xl">Admin Panel</div>
            <button
              onClick={() => setSidebarOpen(!sidebarOpen)}
              className="p-2 rounded hover:bg-gray-200"
            >
              {sidebarOpen ? <X size={24} /> : <Menu size={24} />}
            </button>
          </header>

          <main className="flex-1 overflow-auto p-4">{children}</main>
        </div>
      </div>
    </QueryClientProvider>
  );
}
