/* eslint-disable @typescript-eslint/no-unused-vars */
// frontend/src/app/admin/page.tsx
"use client";

import React, { useEffect, useState, useCallback } from "react";
import axios, { AxiosError } from "axios";
import AdminLayoutClient from "./AdminLayoutClient";
import { useRouter } from "next/navigation";

interface DashboardData {
  totalJobs: number;
  totalApplications: number;
}

const BACKEND_URL = process.env.NEXT_PUBLIC_API_BASE || "http://localhost:5000";

function authHeaders(token: string | null | undefined) {
  return token ? { Authorization: `Bearer ${token}` } : {};
}

export default function AdminPage() {
  const router = useRouter();

  const [verifying, setVerifying] = useState<boolean>(true);
  const [accessToken, setAccessToken] = useState<string | null>(null); // in-memory only
  const [adminVerified, setAdminVerified] = useState<boolean>(false);

  const [dashboardData, setDashboardData] = useState<DashboardData | null>(null);
  const [fetchError, setFetchError] = useState<string | null>(null);

  const [loginEmail, setLoginEmail] = useState<string>("");
  const [loginPassword, setLoginPassword] = useState<string>("");
  const [loginError, setLoginError] = useState<string>("");
  const [loginLoading, setLoginLoading] = useState<boolean>(false);

  /** Try to refresh access token using HttpOnly refresh cookie */
  const tryRefresh = useCallback(async (): Promise<string | null> => {
    try {
      const res = await axios.post(`${BACKEND_URL}/api/admin/refresh`, {}, { withCredentials: true });
      return res.data?.token ?? null;
    } catch {
      return null;
    }
  }, []);

  /** Verify /me using provided access token */
  const verifyWithToken = useCallback(async (token: string): Promise<boolean> => {
    try {
      const meRes = await axios.get(`${BACKEND_URL}/api/admin/me`, {
        headers: authHeaders(token),
        withCredentials: true,
      });
      return !!meRes.data?.success;
    } catch {
      return false;
    }
  }, []);

  /**
   * Generic fetch helper that will attempt one refresh+retry on 401.
   * fn should be a function that accepts a token and performs the desired request.
   */
  const fetchWithAuth = useCallback(
    async <T,>(fn: (token: string) => Promise<T>): Promise<T> => {
      if (!accessToken) throw new Error("No access token available");

      try {
        return await fn(accessToken);
      } catch (err: unknown) {
        if (axios.isAxiosError(err) && err.response?.status === 401) {
          const newToken = await tryRefresh();
          if (!newToken) {
            setAccessToken(null);
            setAdminVerified(false);
            throw new Error("Session expired");
          }
          setAccessToken(newToken);
          return await fn(newToken);
        }
        throw err;
      }
    },
    [accessToken, tryRefresh]
  );

  /** Boot: try refresh -> verify /me */
  useEffect(() => {
    let mounted = true;
    const init = async () => {
      try {
        setVerifying(true);
        const token = await tryRefresh();
        if (!mounted) return;

        if (!token) {
          setAccessToken(null);
          setAdminVerified(false);
          return;
        }

        setAccessToken(token);
        const ok = await verifyWithToken(token);
        if (!mounted) return;
        setAdminVerified(ok);
      } finally {
        if (mounted) setVerifying(false);
      }
    };

    init();
    return () => {
      mounted = false;
    };
  }, [tryRefresh, verifyWithToken]);

  /** Fetch dashboard data after verified */
  useEffect(() => {
    if (!adminVerified || !accessToken) return;

    let mounted = true;
    const fetchDashboard = async () => {
      try {
        setFetchError(null);

        const jobsRes = await fetchWithAuth((token) =>
          axios.get(`${BACKEND_URL}/api/admin/jobs/count`, {
            headers: authHeaders(token),
            withCredentials: true,
          })
        );

        const appsRes = await fetchWithAuth((token) =>
          axios.get(`${BACKEND_URL}/api/admin/applications/count`, {
            headers: authHeaders(token),
            withCredentials: true,
          })
        );

        if (!mounted) return;

        setDashboardData({
          totalJobs: jobsRes.data?.count ?? 0,
          totalApplications: appsRes.data?.count ?? 0,
        });
      } catch (_err: unknown) {
        if (!mounted) return;
        setFetchError("Failed to load dashboard data.");
      }
    };

    fetchDashboard();
    return () => {
      mounted = false;
    };
  }, [adminVerified, accessToken, fetchWithAuth]);

  /** Login handler */
  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoginError("");
    if (!loginEmail.trim() || !loginPassword.trim()) {
      setLoginError("Email and password are required");
      return;
    }

    try {
      setLoginLoading(true);
      const res = await axios.post(
        `${BACKEND_URL}/api/admin/login`,
        { email: loginEmail.trim(), password: loginPassword },
        { withCredentials: true }
      );

      if (!res.data?.success) {
        setLoginError(res.data?.message || "Login failed");
        return;
      }

      const token = res.data?.token as string | undefined;
      if (token) {
        setAccessToken(token);
        const verified = await verifyWithToken(token);
        if (!verified) {
          setLoginError("Unable to verify admin after login");
          setAccessToken(null);
          setAdminVerified(false);
          return;
        }
        setAdminVerified(true);
      } else {
        const refreshed = await tryRefresh();
        if (!refreshed) {
          setLoginError("Login succeeded but unable to obtain access token");
          return;
        }
        setAccessToken(refreshed);
        const verified = await verifyWithToken(refreshed);
        if (!verified) {
          setLoginError("Unable to verify admin after login");
          setAccessToken(null);
          setAdminVerified(false);
          return;
        }
        setAdminVerified(true);
      }

      setLoginEmail("");
      setLoginPassword("");
    } catch (err: unknown) {
      if (axios.isAxiosError(err)) {
        const data = err.response?.data as { message?: string } | undefined;
        setLoginError(data?.message ?? "Server error");
      } else if (err instanceof Error) {
        setLoginError(err.message);
      } else {
        setLoginError("Unexpected error");
      }
    } finally {
      setLoginLoading(false);
    }
  };

  const handleLogout = async () => {
    try {
      await axios.post(`${BACKEND_URL}/api/admin/logout`, {}, { withCredentials: true });
      setAccessToken(null);
      setAdminVerified(false);
    } catch {
      setAccessToken(null);
      setAdminVerified(false);
    }
  };

  if (verifying) {
    return (
      <div className="flex items-center justify-center h-screen text-black-500">
        Verifying session...
      </div>
    );
  }

  if (!adminVerified) {
    return (
      <div className="flex items-center justify-center h-screen bg-blue-100 p-4">
        <form
          onSubmit={handleLogin}
          className="bg-white shadow-lg rounded-xl p-8 w-full max-w-md"
          aria-label="Admin login form"
        >
          <h1 className="text-2xl font-bold mb-6 text-center">Admin Login</h1>

          {loginError && (
            <div className="mb-4 text-sm text-red-700 bg-red-100 p-2 rounded">{loginError}</div>
          )}

          <label className="block mb-2 font-medium">Email</label>
          <input
            type="email"
            value={loginEmail}
            onChange={(e) => setLoginEmail(e.target.value)}
            className="w-full p-2 border rounded mb-4"
            required
            autoComplete="username"
          />

          <label className="block mb-2 font-medium">Password</label>
          <input
            type="password"
            value={loginPassword}
            onChange={(e) => setLoginPassword(e.target.value)}
            className="w-full p-2 border rounded mb-6"
            required
            autoComplete="current-password"
          />

          <button
            type="submit"
            disabled={loginLoading}
            className={`w-full bg-blue-600 text-white p-2 rounded ${
              loginLoading ? "opacity-60 cursor-not-allowed" : "hover:bg-blue-700"
            }`}
          >
            {loginLoading ? "Logging in…" : "Login"}
          </button>
        </form>
      </div>
    );
  }

  return (
    <AdminLayoutClient onLogout={handleLogout}>
      <div className="p-6">
        <h1 className="text-3xl font-bold mb-6">Admin Dashboard</h1>

        {fetchError && <p className="text-red-500 mb-4">{fetchError}</p>}

        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          <div className="bg-white shadow rounded-lg p-6 flex flex-col items-start">
            <h2 className="text-lg font-semibold text-black-600 mb-2">Total Jobs</h2>
            <span className="text-3xl font-bold text-blue-600">
              {dashboardData?.totalJobs ?? "—"}
            </span>
          </div>

          <div className="bg-white shadow rounded-lg p-6 flex flex-col items-start">
            <h2 className="text-lg font-semibold text-black-600 mb-2">Total Applications</h2>
            <span className="text-3xl font-bold text-green-600">
              {dashboardData?.totalApplications ?? "—"}
            </span>
          </div>

          <div className="bg-white shadow rounded-lg p-6 flex flex-col items-start">
            <h2 className="text-lg font-semibold text-black-600 mb-2">Quick Actions</h2>
            <ul className="text-black-700 list-disc list-inside">
              <li>Create Job</li>
              <li>Manage Applications</li>
              <li>Edit Pages</li>
            </ul>
          </div>
        </div>
      </div>
    </AdminLayoutClient>
  );
}
